.container {
    height: 100vh;
    background-color: white
}

.imgLogo {
    width: 200px;
}

.divLogoInput {
    padding-left:70px ;
    width: 100%;
    height: 100px;
    border-bottom: 1px solid #ededed;
    display: flex;
    align-items: center;
    position: fixed;
    background-color: white;
}

.results {
    margin-left: 273px;
    width: 1040px;
    display: flex;
    flex-wrap: wrap;
}

.loaderUtka {
    display: flex;
    width: 200px;
    margin: auto;
    margin-top: 280px;
    
  
}

.divLoaderUtka {
    position: fixed;
    width: 100vw;
    height: 100vh;
    border: 1px solid red;
}

.divTotalItems {
    width: 100vw;
    display: flex;
    margin-top:110px ;
    padding-left: 14.5%;
}

.totalItems {
    color: #979494
}

.imgLoadMore {
   /* margin-left: 750px; */
   margin-bottom: 100px;
   margin-top: 40px;
}
.imgLoadMore:hover {
    cursor: pointer;
}

.selects {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    

}

.divImgLoadMore {
    display: flex;
    margin-left: 40%;
}


@media(min-width: 359px) and (max-width: 1450px) {
    .results {
        width: 72vw;
        margin: auto;
        justify-content: center;
    }
   .divLogoInput {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
   }
}

@media(min-width: 359px) and (max-width: 834px) {
    .divLogoInput {
      height: 250px;
    }
    .divTotalItems {
        margin-top: 260px;
    }
    .divLogoInput {
    width: 100vw;
    padding-left: 7px;
       
    }
 
    
  }

@media(min-width: 834px) and (max-width: 1451px) {
  .divLogoInput {
    height: 200px;
    padding-left: 7px;
  }
  .divTotalItems {
    margin-top: 100px;
}
      
}

