.container {
    width: 230px;
    margin: 15px;   
    display: flex;
    align-self: baseline;
    flex-direction: column;
    background-color:#f3f2f1;
   
}

.pointer {
    pointer-events: none
}
.divImg {
    width: 100%;
    height: 215px;
    display: flex;
    justify-content: center;
    margin-top: 30px;
    margin-bottom: 20px;
}
.divImg:hover {
    cursor: pointer;
}
.img {
   width: 150px;
}

.divCategories {
    text-decoration: underline;
    margin: 5px;
    color: #898a8d
}

.divTitle {
    margin: 7px;
    font-size: 20px;
}

.divAutors {
    margin-bottom: 20px;
}

.spanAutors {
    color:  #898a8d;
    margin: 7px;
}