.input {
    margin-left: 20px;
    width: 503px;
    height: 34px;
    font-size: 20px;
    font-weight: normal;
    border: 0;
    outline: none;
}

.divRemove {
    margin: auto;
    font-size: 20px;
    color: #808080;
    background-color: white;
    border: 0px;
}

.clue {
    position: absolute;
    margin-left: 550px;
    margin-top: 5px;
    border: 1px solid black;
    padding: 3px;
    color: white;
    background-color: black;
}

.divRemove:hover {
    cursor: pointer;
}

.divInputIcon {
    display: flex;
    align-items: center;
    width: 612px;
    height: 44px;
    border: 2px solid #e3e4e8;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    border-bottom-left-radius: 50px;
    border-top-left-radius: 50px;
    
}

.imgSearch {
    width: 30px;
    height: 30px;
    margin-top: 5px;
    margin-left: 5px;
}

.divInputIcon:hover {
    box-shadow: 0px 0px 6px#cccaca;
}

.error {
    position: absolute;
    color: #808080
}

.divConteinerClue {
    display: flex;
    justify-content: flex-end;
    width: 30vw;
    height: 20px;
    position: absolute;
   }

@media(min-width: 359px) and (max-width: 612px) {
   .divInputIcon {
    margin: auto;
    width: 95vw;
   }
   .input {
    width: 72vw
   }
   .divConteinerClue {
    width: 95vw;
    }  
     
}

@media(min-width: 612px) and (max-width: 1920px) {
     .divConteinerClue {
       width: 612px;
    }  
 }