.book {
    width: 100%;
    height: 100vh;
    display: flex;
}

.imgBook {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f3f2f1;
    margin-top: 100px;
}

.imgValue {
    width: 270px;
}

.BookValue {
    width: 50%;
    display: flex;
    flex-direction: column;
    padding-left: 20px;
    margin-top: 100px;
}

.backContainer {
    width: 100vw;
    position: absolute;
    margin-top: 100px;
    display: flex;
    justify-content: end;
    
}

.back {
    color: grey;
    width: 20px;
    font-size: 30px;
    background-color: rgb(224, 226, 226);
    margin-right: 20px;
    position: fixed;
    
}

.back:hover {
    cursor: pointer;
}

.categories {
    padding-top: 50px;
    font-size: 30px;
    color:   #979494;
    margin-left: 40px;
}

.title {
    font-size: 50px;
    padding-top: 40px;
    margin-left: 40px;
}

.authors {
    font-size: 30px;
    padding-top: 40px;
    color:  #979494;
    margin-left: 40px;
}

.opisanie {
    width:90%;
    margin: 80px auto;
    padding: 10px;
    border: 1px solid rgb(214, 212, 212);
}

@media(min-width: 359px) and (max-width: 1000px) {
  .book {
    margin-top: 190px;
    display: flex;
    flex-direction: column;
    align-items: center;
    
   }
  .BookValue {
    width: 100vw;
  }
  .imgBook {
     width: 100vw;
    }
  .opisanie {
       width: 90%;
       text-align: justify;
    }
}